<template>
  <n-card title="" embedded>
    <n-space vertical size="large">
      <!--  -->
      <n-form-item label="">
        <n-radio-group v-model:value="selectedMethod">
          <n-space>
            <n-radio value="otp"> OTP () </n-radio>
            <n-radio value="webauthn"> WebAuthn () </n-radio>
          </n-space>
        </n-radio-group>
      </n-form-item>

      <!-- OTP  -->
      <div v-if="selectedMethod === 'otp'">
        <n-space vertical size="large">
          <n-alert type="info">
             TOTP  Google AuthenticatorAuthy
          </n-alert>

          <div v-if="loading" class="text-center">
            <n-spin size="large" />
          </div>

          <div v-else-if="otpSecret" class="text-center">
            <!--  -->
            <div class="qr-code-container">
              <canvas ref="qrCanvas"></canvas>
            </div>

            <!--  -->
            <n-collapse>
              <n-collapse-item title="">
                <n-input :value="otpSecret" readonly>
                  <template #suffix>
                    <n-button text @click="copySecret">  </n-button>
                  </template>
                </n-input>
              </n-collapse-item>
            </n-collapse>

            <!--  -->
            <n-form-item label="" class="mt-4">
              <n-input
                v-model:value="otpCode"
                placeholder="6"
                :maxlength="6"
                @keydown.enter="handleOTPVerify"
              />
            </n-form-item>
          </div>

          <n-button v-if="!otpSecret" type="primary" @click="handleOTPInit" :loading="loading">
            
          </n-button>

          <n-button
            v-else
            type="primary"
            @click="handleOTPVerify"
            :loading="verifying"
            :disabled="otpCode.length !== 6"
          >
            
          </n-button>
        </n-space>
      </div>

      <!-- WebAuthn  -->
      <div v-else-if="selectedMethod === 'webauthn'">
        <n-space vertical size="large">
          <n-alert type="info">
            WebAuthn Face IDWindows Hello 
          </n-alert>

          <n-alert v-if="!isWebAuthnSupported" type="warning">  WebAuthn </n-alert>

          <n-button v-else type="primary" @click="handleWebAuthnSetup" :loading="verifying">
            
          </n-button>
        </n-space>
      </div>
    </n-space>
  </n-card>
</template>

<script setup lang="ts">
import { ref, onMounted, watch } from 'vue'
import { useMessage } from 'naive-ui'
import QRCode from 'qrcode'
import { startRegistration } from '@simplewebauthn/browser'
import { twoFAApi } from '@/common/api'

const message = useMessage()

// 
const selectedMethod = ref<'otp' | 'webauthn'>('otp')

// OTP 
const loading = ref(false)
const verifying = ref(false)
const otpSecret = ref('')
const otpCode = ref('')
const qrCanvas = useTemplateRef<HTMLCanvasElement>('qrCanvas')

// WebAuthn 
const isWebAuthnSupported = ref(false)

const emit = defineEmits<{
  success: [token: string]
}>()

//  OTP
const handleOTPInit = async () => {
  loading.value = true
  try {
    const res = await twoFAApi.setupOTPInit()
    if (res.code === 0) {
      otpSecret.value = res.data.secret
      const qrCodeURL = res.data.qrCodeURL
      nextTick(() => {
        if (qrCanvas.value) {
          QRCode.toCanvas(qrCanvas.value, qrCodeURL, {
            width: 256,
            margin: 2,
          })
        }
      })
    } else {
      message.error(res.msg || '')
    }
  } catch (error: any) {
    message.error(error.message || '')
  } finally {
    loading.value = false
  }
}

//  OTP
const handleOTPVerify = async () => {
  if (otpCode.value.length !== 6) {
    message.warning('6')
    return
  }

  verifying.value = true
  try {
    const res = await twoFAApi.setupOTPVerify(otpCode.value, otpSecret.value)
    if (res.code === 0) {
      message.success('')
      emit('success', res.data.token)
    } else {
      message.error(res.msg || '')
    }
  } catch (error: any) {
    message.error(error.message || '')
  } finally {
    verifying.value = false
  }
}

// 
const copySecret = () => {
  navigator.clipboard.writeText(otpSecret.value)
  message.success('')
}

// WebAuthn 
const handleWebAuthnSetup = async () => {
  verifying.value = true
  try {
    // 
    const beginRes = await twoFAApi.webauthnRegisterBegin()
    if (beginRes.code !== 0) {
      message.error(beginRes.msg || '')
      return
    }

    const options = beginRes.data.options
    const sessionData = beginRes.data.sessionData
    //  WebAuthn API
    const attResp = await startRegistration({
      optionsJSON: options.publicKey,
    })

    // 
    const finishRes = await twoFAApi.webauthnRegisterFinish(sessionData, attResp)
    if (finishRes.code === 0) {
      message.success('')
      emit('success', finishRes.data.token)
    } else {
      message.error(finishRes.msg || '')
    }
  } catch (error: any) {
    console.error('WebAuthn setup error:', error)
    message.error(error.message || 'WebAuthn ')
  } finally {
    verifying.value = false
  }
}

//  WebAuthn 
onMounted(() => {
  isWebAuthnSupported.value = window.PublicKeyCredential !== undefined
})

// 
watch(selectedMethod, () => {
  otpSecret.value = ''
  otpCode.value = ''
  loading.value = false
  verifying.value = false
})
</script>

<style scoped>
.qr-code-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.text-center {
  text-align: center;
}

.mt-4 {
  margin-top: 16px;
}
</style>
