<template>
  <n-card title="" embedded>
    <n-space vertical size="large">
      <n-alert type="info">  </n-alert>

      <!-- OTP  -->
      <div v-if="method === 'otp'">
        <n-form-item label="">
          <n-input
            v-model:value="otpCode"
            placeholder="6"
            :maxlength="6"
            size="large"
            @keydown.enter="handleOTPVerify"
          />
        </n-form-item>

        <n-button
          type="primary"
          block
          size="large"
          @click="handleOTPVerify"
          :loading="verifying"
          :disabled="otpCode.length !== 6"
        >
          
        </n-button>
      </div>

      <!-- WebAuthn  -->
      <div v-else-if="method === 'webauthn'">
        <n-alert v-if="!isWebAuthnSupported" type="warning">  WebAuthn </n-alert>

        <n-button
          v-else
          type="primary"
          block
          size="large"
          @click="handleWebAuthnVerify"
          :loading="verifying"
        >
          
        </n-button>
      </div>
    </n-space>
  </n-card>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import { useMessage } from 'naive-ui'
import { startAuthentication } from '@simplewebauthn/browser'
import { twoFAApi } from '@/common/api'

const message = useMessage()

interface Props {
  method: string
}

defineProps<Props>()

// 
const verifying = ref(false)
const otpCode = ref('')
const isWebAuthnSupported = ref(false)

const emit = defineEmits<{
  success: [token: string]
}>()

//  OTP
const handleOTPVerify = async () => {
  if (otpCode.value.length !== 6) {
    message.warning('6')
    return
  }

  verifying.value = true
  try {
    const res = await twoFAApi.verifyOTP(otpCode.value)
    if (res.code === 0) {
      message.success('')
      emit('success', res.data.token)
    } else {
      message.error(res.msg || '')
      otpCode.value = '' // 
    }
  } catch (error: any) {
    message.error(error.message || '')
    otpCode.value = '' // 
  } finally {
    verifying.value = false
  }
}

// WebAuthn 
const handleWebAuthnVerify = async () => {
  verifying.value = true
  try {
    // 
    const beginRes = await twoFAApi.webauthnLoginBegin()
    if (beginRes.code !== 0) {
      message.error(beginRes.msg || '')
      return
    }

    const options = beginRes.data.options
    const sessionData = beginRes.data.sessionData

    //  WebAuthn API
    const asseResp = await startAuthentication({ optionsJSON: options.publicKey })

    // 
    const finishRes = await twoFAApi.webauthnLoginFinish(sessionData, asseResp)
    if (finishRes.code === 0) {
      message.success('')
      emit('success', finishRes.data.token)
    } else {
      message.error(finishRes.msg || '')
    }
  } catch (error: any) {
    console.error('WebAuthn verify error:', error)
    message.error(error.message || 'WebAuthn ')
  } finally {
    verifying.value = false
  }
}

//  WebAuthn 
onMounted(() => {
  isWebAuthnSupported.value = window.PublicKeyCredential !== undefined
})
</script>
