<template>
  <div class="login-page">
    <div class="login-container">
      <!-- Logo  -->
      <div class="login-header">
        <img src="/logo.svg" alt="Watch Docker" class="logo" />
        <div>
          <n-h2 class="app-title">Watch Docker</n-h2>
          <span class="text-subtitle"></span>
        </div>
      </div>

      <!--  -->
      <n-card class="login-card" embedded v-if="!authStore.twoFARequired">
        <n-form
          ref="formRef"
          :model="loginForm"
          :rules="rules"
          label-placement="top"
          require-mark-placement="right-hanging"
          size="large"
          :show-feedback="false"
          :show-label="false"
        >
          <n-form-item label="" path="username" class="pb-2">
            <n-input
              v-model:value="loginForm.username"
              placeholder=""
              :disabled="loginLoading"
              @keydown.enter="handleLogin"
            />
          </n-form-item>

          <n-form-item label="" path="password" class="pb-2">
            <n-input
              v-model:value="loginForm.password"
              type="password"
              placeholder=""
              show-password-on="click"
              :disabled="loginLoading"
              @keydown.enter="handleLogin"
            />
          </n-form-item>

          <n-form-item>
            <n-checkbox v-model:checked="rememberUsername" :disabled="loginLoading">
              
            </n-checkbox>
          </n-form-item>

          <n-form-item>
            <n-button
              type="primary"
              block
              size="large"
              :loading="loginLoading"
              @click="handleLogin"
            >
              
            </n-button>
          </n-form-item>
        </n-form>
      </n-card>

      <!--  -->
      <div v-else>
        <!--  -->
        <TwoFASetup v-if="authStore.twoFASetupRequired" @success="handleTwoFASuccess" />

        <!--  -->
        <TwoFAVerify v-else :method="authStore.twoFAMethod" @success="handleTwoFASuccess" />
      </div>

      <!--  -->
      <div class="system-info" v-if="settingStore.systemInfo">
        <n-card size="small" embedded>
          <n-space justify="space-between" align="center">
            <n-text depth="3">Docker: {{ settingStore.systemInfo.dockerVersion }}</n-text>
            <n-text depth="3">{{ settingStore.systemInfo.version }}</n-text>
          </n-space>
        </n-card>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, watch } from 'vue'
import { useRouter } from 'vue-router'
import { useMessage, type FormInst, type FormRules } from 'naive-ui'
import { useAuthStore } from '@/store/auth'
import { useSettingStore } from '@/store/setting'
import TwoFASetup from '@/components/TwoFASetup.vue'
import TwoFAVerify from '@/components/TwoFAVerify.vue'

// 
const router = useRouter()
const message = useMessage()

// 
const authStore = useAuthStore()
const settingStore = useSettingStore()

// 
const formRef = ref<FormInst | null>(null)
const loginForm = ref({
  username: '',
  password: '',
})

// 
const loginLoading = ref(false)
const rememberUsername = ref(false)

// 
const rules: FormRules = {
  username: {
    required: true,
    message: '',
    trigger: ['input', 'blur'],
  },
  password: {
    required: true,
    message: '',
    trigger: ['input', 'blur'],
  },
}

// 
const loadSavedUsername = () => {
  if (settingStore.setting.rememberUsername) {
    loginForm.value.username = settingStore.setting.rememberedUsername
    rememberUsername.value = true
  }
}

// 
const handleLogin = async () => {
  try {
    await formRef.value?.validate()
    loginLoading.value = true

    const result = await authStore.login(loginForm.value.username, loginForm.value.password)

    if (result.success) {
      // 
      settingStore.saveRememberedUsername(loginForm.value.username, rememberUsername.value)

      // 
      if (result.needTwoFA) {
        message.info(result.isSetup ? '' : '')
      } else {
        message.success('')
        // 
        router.push('/')
      }
    } else {
      // 
      if (!rememberUsername.value) {
        settingStore.clearRememberedUsername()
      }
      message.error(result.message || '')
    }
  } catch (error) {
    console.error('Login validation failed:', error)
    // 
    if (!rememberUsername.value) {
      settingStore.clearRememberedUsername()
    }
  } finally {
    loginLoading.value = false
  }
}

// 
const handleTwoFASuccess = (token: string) => {
  authStore.completeTwoFA(token)
  message.success('')
  router.push('/')
}

// 
watch(rememberUsername, (newValue) => {
  // 
  if (!newValue) {
    settingStore.clearRememberedUsername()
  }
})

// 
onMounted(() => {
  // 
  loadSavedUsername()
  // 
  settingStore.fetchSystemInfo()
})
</script>

